/**
 * A button element
 * @param {object} props This will contain information to create a JSX button element
 */
class DrumPad extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isActive: false
    };
    this.playSound = this.playSound.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
  }
  handleKeyPress(e) {
    if (e.keyCode === this.props.trigger) {
      this.playSound();
    } 
  }
  playSound(e) {
    this.setState({isActive: true});
    const sound = document.getElementById(this.props.displayId);
    // @ts-ignore
    sound.currentTime=0;
    // @ts-ignore
    sound.play();
  }
  render() {
    return (
      <button id={this.props.id} className="drum-pad" onClick={this.playSound}>
        <audio
          className="clip"
          id={this.props.displayId}
        ><source src={this.props.audioFile} type="audio/mpeg"/></audio>
        {this.props.displayId}
      </button>
    );
  }
}

